@use './../utilities/variables' as *;

.loader {margin: 40px auto 40px auto;
    border: 14px solid $color-accent-orange;
    border-top: 14px solid rgb(12, 8, 56);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}